.customer-reviews {
    background-image: url(../images/customer-reviewes/ice-cream-bg-x1.png);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;

    @include retina {
        background-image: url(../images/customer-reviewes/ice-cream-bg-x2.png);
    }
}

.customer-reviews__container {
    padding-top: 80px;
    padding-bottom: 48px;
    
    @include tablet{
        padding-top: 60px;
        padding-bottom: 80px;
    }

    @include desktop{
        padding: 128px 116px 100px 116px;
    }
}


.customer-reviews__image {
    margin: 0 auto 32px auto;
    border-radius: 50%;

    @include tablet{
        margin-bottom: 28px;
    }

    @include desktop {
        margin-bottom: 32px;
    }

}


.customer-reviews__text {
    font-size: 12px;
    line-height: 2.55;
    text-align: center;
    color: var(--light-color);

    padding: 34px 39px;
    background-color: var(--primary-bg-color);
    border-radius: 10px;
    margin-bottom: 30px;

    @include tablet{
        font-weight: 500;
        font-size: 14px;
        line-height: 1.86;
    
        letter-spacing: 0.04em;
                    
        margin-bottom: 32px;
        padding: 29px 80px 28px 80px;
    }

    @include desktop {
        font-weight: 500;
        font-size: 16px;
        line-height: 1.86;

        margin-bottom: 32px;
        padding: 39px 109px 47px 109px;
    }
}


.customer-reviews__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.85;
    text-align: center;
}

.swiper-pagination {
    position: static;
    margin-top: 14px;

    @include tablet{
        margin-top: 32px;
    }
}

.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #D9D9D9;
    ;
}

.swiper-pagination-bullet-active {
    background-color: var(--primary-bg-color);
}